import * as React from 'react'; import Typography from '@mui/material/Typography';  import Glink from '../components/SzGlink'; 
import BottomNavigation from '@mui/material/BottomNavigation'; import BottomNavigationAction from '@mui/material/BottomNavigationAction'; 
import Paper from '@mui/material/Paper'; import SvgIcon from '@mui/material/SvgIcon'; 
//import MuiLink from '@mui/material/Link';

function LightBulbIcon(props) {return ( <SvgIcon {...props}> <path d="M9 21c0 .55.45 1 1 1h4c.55 0 1-.45 1-1v-1H9v1zm3-19C8.14 2 5 5.14 5 9c0 2.38 1.19 4.47 3 5.74V17c0 .55.45 1 1 1h6c.55 0 1-.45 1-1v-2.26c1.81-1.27 3-3.36 3-5.74 0-3.86-3.14-7-7-7zm2.85 11.1l-.85.6V16h-4v-2.3l-.85-.6C7.8 12.16 7 10.63 7 9c0-2.76 2.24-5 5-5s5 2.24 5 5c0 1.63-.8 3.16-2.15 4.1z" />  </SvgIcon> );}
const FacebookIcon = () => (<SvgIcon> <path d="M9 8h-3v4h3v12h5v-12h3.642l.358-4h-4v-1.667c0-.955.192-1.333 1.115-1.333h2.885v-5h-3.808c-3.596 0-5.192 1.583-5.192 4.615v3.385z" /></SvgIcon>);
const TwitterIcon = () => ( <SvgIcon> <path d="M24 4.557c-.883.392-1.832.656-2.828.775 1.017-.609 1.798-1.574 2.165-2.724-.951.564-2.005.974-3.127 1.195-.897-.957-2.178-1.555-3.594-1.555-3.179 0-5.515 2.966-4.797 6.045-4.091-.205-7.719-2.165-10.148-5.144-1.29 2.213-.669 5.108 1.523 6.574-.806-.026-1.566-.247-2.229-.616-.054 2.281 1.581 4.415 3.949 4.89-.693.188-1.452.232-2.224.084.626 1.956 2.444 3.379 4.6 3.419-2.07 1.623-4.678 2.348-7.29 2.04 2.179 1.397 4.768 2.212 7.548 2.212 9.142 0 14.307-7.721 13.995-14.646.962-.695 1.797-1.562 2.457-2.549z"  /></SvgIcon>);

const SzFooter = ({ facebookUrl, githubUrl, twitterUrl }) => {
  const openSite = (site) => { if (site) { window.open(site, '_blank'); } };  return (
    <Paper sx={{position: 'fixed', bottom: 0, left: 0, right: 0, }}  elevation={3}  >

        <Typography color="text.secondary" sx={{ flexGrow: 1,  textAlign: "center", MB:4 }} >
        <LightBulbIcon sx={{ mr: 1, verticalAlign: 'middle' }} />
        <Glink to="/about">{'Click here to contact us. Copyright © '}{new Date().getFullYear()}{'. BWF OP Group'}</Glink></Typography>
        <br /><br /><br />
    </Paper>  );};

export default SzFooter;
